.navbar {
  position: relative;
  width: 100%;
  height: 130px;
  top: 0;
  background-color: #52545f;
  box-shadow: 0px 5px 14px 2px rgba(0, 0, 0, 0.25);
  border-radius: 0px 0px 38px 38px;
  transition: ease 0.3s;
}

.navbar-container {
  width: 100%;
  height: 100%;
  margin: 0px auto;
  position: relative;
  display: flex;
  align-items: flex-start;
}

.logo-container {
  position: absolute;
  margin: auto;
  width: 100%;
  top: 36px;
  display: flex;
  justify-content: center;
}

@media (min-width: 768px) {
  .navbar-container {
    width: 768px;
  }
}
